import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { FooterModule } from '../share/footer/footer.module'
import { AppComponent } from './app.component';

const routes: Routes = [
  {
    path: '',
    component: AppComponent,
    children: [
      { path: '', redirectTo: 'home', pathMatch: "full" },
      { path: 'home', loadChildren: () => import('./home/home.module').then(m => m.HomeModule) },
      { path: 'about-me', loadChildren: () => import('./aboutme/aboutme.module').then(m => m.AboutmeModule) },
      { path: 'experience', loadChildren: () => import('./exp/exp.module').then(m => m.ExpModule) },
      { path: 'mana', loadChildren: () => import('./mana/mana.module').then(m => m.ManaModule) },
      { path: 'portfolio', loadChildren: () => import('./portfolio/portfolio.module').then(m => m.PortfolioModule) }
    ]
  },

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
